<template>
	<p class="pp">归属地查询</p>
	<DIV style="width: 400PX;margin: 0 AUTO;">
		<el-form :label-position="labelPosition" label-width="80px" :model="ruleForm" :rules="rules" ref="ruleForm">
			<el-form-item label="手机" prop="phone">
				<el-input v-model="ruleForm.phone" style="width: 320PX;" maxlength="11"></el-input>
			</el-form-item>
			<el-form-item style="margin-top: 8VH;">
				<el-button type="primary" plain>查询归属地</el-button>
			</el-form-item>

		</el-form>
	</DIV>

</template>

<script>
	export default {
		name: 'phone',
		data: function() {
			return {
				ruleForm: {
					phone: '',
				},
				rules: {
					phone: [{
							required: true,
							message: '请输入手机号',
							trigger: 'blur'
						},
						{
							min: 11,
							max: 11,
							message: '长度在11个字符',
							trigger: 'blur'
						}
					],
				}
			}
		}
	}
</script>

<style>
	.ppp {
		font-size: 3vw;
		font-family: fantasy;
		font-weight: bold;
		color: white;
		background-image: linear-gradient(to right,
				orangered,
				orange,
				gold,
				lightgreen,
				cyan,
				dodgerblue,
				mediumpurple,
				hotpink,
				orangered);
		background-size: 110vw;
		-webkit-background-clip: text;
		color: transparent;
		animation: sliding 30s linear infinite;
	}

	@keyframes sliding {
		to {
			background-position: -2000vw;
		}
	}
</style>
